import { containedByNativePath, toPathnames } from "../fs/Path"
import { SimpleFileOrString } from "../fs/SimpleFile"
import { cacheDir } from "./CacheDir"
import { libraryDataDir, libraryPreviewsDir } from "./LibraryDirs"

export function inHiddenPhotoStructureDir(child: SimpleFileOrString): boolean {
  if (
    toPathnames(child).some(ea =>
      ea.toLowerCase().startsWith(".photostructure")
    )
  ) {
    return true
  }

  for (const dir of [libraryDataDir, libraryPreviewsDir, cacheDir]) {
    if (containedByNativePath({ descendant: child, ancestor: dir() })) {
      return true
    }
  }

  return false
}
