import { isNotEmpty, uniq } from "../../fe/Array"
import { b, li, tt } from "../../fe/BabyMarkdown"
import { minuteMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { Link } from "../../fe/HealthChecks"
import { IconName } from "../../fe/IconName"
import { map } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { GB, fmtBytes } from "../../fe/Units"
import { userid } from "../UserInfo"
import { libraryDirPosixFile, setupLibraryDirs_ } from "../dir/LibraryDirs"
import { getDevEnvFlag } from "../env/DevEnvFlags"
import { errorToS } from "../error/Error"
import { isStatRWX } from "../fs/Access"
import { PosixFile } from "../fs/PosixFile"
import { isDocker } from "../platform/isDocker"
import { Settings } from "../settings/Settings"
import { libraryHasSettings } from "../settings/SettingsIO"
import { Volume } from "../volumes/Volume"
import { bestVolumeForPath } from "../volumes/Volumes"
import { HealthCheck, HealthCheckTest } from "./HealthCheck"

export function libraryDirHealthChecks() {
  return [libraryDirHealthCheck(), libraryDirFreeCheck()]
}

async function testLibraryDirDocker(
  libraryDir: Maybe<PosixFile>
): PromiseMaybe<HealthCheckTest> {
  if (libraryDir == null) {
    return {
      level: "error",
      msg: `Something's amiss: the default library directory is blank. (Did you set the ${tt(
        "PS_LIBRARY_DIR"
      )} environment variable?)`
    }
  }
  let err: Maybe<Error>
  try {
    if (await libraryDir.isReadWriteExecutable_()) {
      return
    }
  } catch (error: any) {
    err = error
  }
  return {
    level: "error",
    msg: [
      `Your library directory, ${tt(
        libraryDir
      )}, is not read-write-execute for ${tt("userid:" + userid())}.`,
      map(err, errorToS),
      "Please fix the permissions for this bind mount."
    ]
  }
}

const libraryDirHealthCheck = defer(() => {
  return HealthCheck.for({
    section: "Library",
    id: "library-health",
    pendingMsg: "Checking library path",
    ttlMs: minuteMs,
    links: [
      {
        text: `What's a "PhotoStructure library?"`,
        icon: "logo" as IconName,
        url: "https://photostructure.com/faq/library/"
      },
      ...(isDocker()
        ? [
            {
              text: `Docker setup`,
              url: "https://photostructure.com/server/photostructure-for-docker/"
            }
          ]
        : [])
    ] as Link[],
    okLinks: [
      {
        type: "button",
        text: "Run library maintenance jobs",
        url: "/admin/run-maintenance",
        icon: "handyman"
      }
    ],
    later: async () => {
      const libraryDir = libraryDirPosixFile()
      if (isDocker()) {
        const test = await testLibraryDirDocker(libraryDir)
        if (test != null) return test
      } else {
        // We're not docker, so the library may be blank if we haven't gone
        // through setup yet.
        // HEY FUTURE ME THIS ORDER ACTUALLY IS WHAT I MEANT.
        // If the directory exists, but permissions are wrong, I want to return
        // an error and give them a chance to fix it.
        if (libraryDir != null) {
          if (!(await libraryDir.exists())) {
            return {
              level: "error",
              msg: `Library ${tt(libraryDir)} is missing.`
            }
          }
          if (!(await libraryDir.isDirectory())) {
            return {
              level: "error",
              msg: `Library ${tt(libraryDir)} is not a directory.`
            }
          }
        }
      }

      if (libraryDir != null && !(await libraryDir.isReadWriteDirectory())) {
        const msg = ["Library is not read/writable by the current user."]
        try {
          const stat = await libraryDir.stat_()
          msg.push(`${tt(libraryDir)} is owned by ${tt("userid:" + stat.uid)}.`)
        } catch (error: any) {
          msg.push(`Failed to stat() ${tt(libraryDir)}: ${errorToS(error)}`)
        }
        return {
          level: "error",
          msg
        }
      }

      // if we got to here, the directory is either missing or is read/write.
      // If we have settings, we're all good:
      if (!libraryHasSettings()) {
        return {
          level: "no-library",
          msg: [
            "No PhotoStructure library is currently open.",
            Settings.libraryDir.hasValue()
              ? tt(Settings.libraryDir.toEnvLine())
              : undefined
          ]
        }
      }

      // OK, so we _do_ have a library. Are all the permissions ok?
      // let HealthCheck catch and format any rejections:
      const dirs = await setupLibraryDirs_()
      for (const dir of dirs) {
        try {
          const stat = await dir.stat_()
          if (!isStatRWX(stat)) {
            return {
              level: "warn",
              msg: `The library directory, ${tt(
                dir
              )}, has incorrect permissions. Current owner: ${tt(
                "userid:" + stat.uid
              )}, permissions: ${tt(
                "0x" + stat.mode.toString(8)
              )}). PhotoStructure is running as ${tt(
                "userid:" + userid()
              )}. Please fix these permissions and restart PhotoStructure, or pick a new library directory.`
            }
          }
        } catch (error) {
          return {
            level: "error",
            msg: `Failed to read ${dir.nativePath}: ${errorToS(error)}`
          }
        }
      }

      // Are the directories from bind mounts?
      if (isDocker()) {
        const badDirs: string[] = []
        // let HealthCheck catch and format any rejections:
        for (const pf of await setupLibraryDirs_()) {
          const vol = await bestVolumeForPath(pf)
          if (vol == null) {
            return {
              level: "warn",
              msg: `Could not check for docker volumes: no volume found for library path ${tt(
                pf
              )}`
            }
          }
          if (vol.mountpoint === "/") {
            badDirs.push(pf.nativePath)
          }
        }
        if (isNotEmpty(badDirs)) {
          return {
            level: "warn",
            msg: [
              `The following directories are not in Docker volumes:`,
              li(badDirs),
              `Please add bind mounts for these directories and restart the docker container.`
            ]
          }
        }
      }

      return {
        level: "ok",
        msg: [
          `Your PhotoStructure library directories are in order.\nVerified the following directories are ${tt(
            "-rwX"
          )} and owned by ${tt("userid:" + userid())}`,
          li(
            uniq([libraryDir, ...dirs])
              .sort()
              .map(tt)
          )
        ]
      }
    }
  })
})

const libraryDirFreeCheck = defer(() =>
  HealthCheck.for({
    section: "Library",
    id: "volume-disk-free",
    pendingMsg: "Checking free space on library volume(s)",
    ttlMs: 5 * minuteMs,

    later: async () => {
      if (!gt0(Settings.minDiskFreeGb.valueOrDefault)) {
        return {
          level: "disabled",
          msg: `Library disk free test disabled: ${tt(
            Settings.minDiskFreeGb.key + "=0"
          )}`
        }
      }
      if (!libraryHasSettings()) {
        return {
          level: "disabled",
          msg: `Library disk free test disabled: no library is open`
        }
      }

      const toCheck: {
        vol: Volume
        paths: string[]
      }[] = []

      for (const pf of await setupLibraryDirs_()) {
        const vol = await bestVolumeForPath(pf)
        if (vol == null) {
          return {
            level: "warn",
            msg: `Could not check for minimum disk free: no volume found for library path ${tt(
              pf
            )}`
          }
        }
        let o = toCheck.find(ea => ea.vol.mountpoint === vol.mountpoint)
        if (o == null) {
          o = {
            vol,
            paths: []
          }
          toCheck.push(o)
        }
        o.paths.push(pf.nativePath)
      }

      const minFreeBytes = Settings.minDiskFreeGb.valueOrDefault * GB
      const descriptions: string[] = []

      for (const { vol, paths } of toCheck) {
        const desc =
          b(
            `Volume ${tt(vol.mountpoint)} has ${fmtBytes(vol.available)} free.`
          ) +
          `\nThis volume is used for these PhotoStructure directories:${li(
            uniq(paths).sort().map(tt)
          )}`

        if (
          vol.available < minFreeBytes ||
          getDevEnvFlag("PS_FORCE_FULLDISK")
        ) {
          return {
            level: "stop-sync",
            msg:
              "Library volume is almost full\n" +
              tt(Settings.minDiskFreeGb.key) +
              " is set to " +
              fmtBytes(minFreeBytes) +
              ".\n" +
              desc +
              "\nPhotoStructure sync is automatically paused until you free up at least " +
              fmtBytes(minFreeBytes - vol.available) +
              " on " +
              vol.mountpoint
          }
        } else {
          descriptions.push(desc)
        }

        if (vol.available < minFreeBytes * 0.75) {
          return {
            level: "warn",
            msg: "Library volume is almost full" + desc
          }
        }
      }
      return {
        level: "ok",
        msg:
          "Library volumes have sufficient free space.\n" +
          descriptions.join("\n") +
          "\nPhotoStructure sync will pause if free space drops below " +
          tt(Settings.minDiskFreeGb.key) +
          ", set to " +
          fmtBytes(minFreeBytes) +
          "."
      }
    }
  })
)
