import { blank } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { sortByAsync } from "../async/Promise"
import { inHiddenPhotoStructureDir } from "../dir/InHiddenPhotoStructureDir"
import { BaseFile } from "../fs/BaseFile"
import {
  extAliases,
  isExt,
  isSidecarExt,
  mkExtsRegExp,
  normalizeExt
} from "../fs/FileExt"
import { stripCopySuffixFromName } from "../fs/FuzzyNames"
import { SimpleFile } from "../fs/SimpleFile"
import { Settings } from "../settings/Settings"
import {
  commonPrefixIgnoreCase,
  ensurePrefix,
  equalsIgnoreCase
} from "../String"

function strEql(a: string, b: string, { ignoreCase }: { ignoreCase: boolean }) {
  return ignoreCase
    ? equalsIgnoreCase(a, b, { normalize: false /* we did it already */ })
    : a === b
}

export function isSidecarOf(src: SimpleFile, sidecar: SimpleFile) {
  // Must have a sidecar extension and not be hidden:
  if (!isSidecarExt(sidecar.ext) || sidecar.base.startsWith(".")) return false

  let srcName = src.name
  let sidecarName = sidecar.name

  const isJson = isExt(sidecar.ext, "json")
  const ignoreCase =
    isJson || Settings.matchSidecarsCaseInsensitively.valueOrDefault
  if (ignoreCase) {
    // if the files are coming from the same filesystem, we don't expect
    // different unicode encodings, so we don't need to do (expensive!)
    // normalization:
    srcName = srcName.normalize()
    sidecarName = sidecarName.normalize()
  }

  if (strEql(srcName, sidecarName, { ignoreCase })) {
    return true
  }

  // match image.JPG with image.jpeg.xmp (different spelling of the same file extension type)
  const aliases = map(normalizeExt(src.ext), ea => extAliases().get(ea))
  // we really _should_ be able to find an extension here:
  if (aliases != null) {
    sidecarName = sidecarName.replace(mkExtsRegExp(aliases), "")
  }

  if (strEql(srcName, sidecarName, { ignoreCase })) {
    return true
  }

  const fuzzy = isJson || Settings.matchSidecarsFuzzily.valueOrDefault

  if (fuzzy) {
    srcName = stripCopySuffixFromName(srcName)
    sidecarName = stripCopySuffixFromName(sidecarName)
  }

  return (
    strEql(srcName, sidecarName, { ignoreCase }) ||
    (isJson && isJsonFuzzyMatch(srcName, sidecarName))
  )
}

export const AllowedUncommonSuffixRe =
  /^((-edit(ed)?)|(-?\w{1,2})|(-?\(\d{1,2}\)))$/i

function isJsonFuzzyMatch(srcName: string, sidecarName: string): boolean {
  // guh, we have to do some b&115h!t for Google.
  // https://forum.photostructure.com/t/creation-time-formatted-time-total-confusion-or-google-takeout-sidecar-files-are-misnamed/574/16?u=mrm
  // https://forum.photostructure.com/t/incorrect-tags-assigned/842

  const commonIdx = commonPrefixIgnoreCase(srcName, sidecarName)
  if (commonIdx.length < 8) return false
  function suffixOK(suffix: string) {
    return blank(suffix) || suffix.match(AllowedUncommonSuffixRe) != null
  }
  const aSuffix = srcName.slice(commonIdx.length)
  const aSuffixOK = suffixOK(aSuffix)

  const bSuffix = sidecarName.slice(commonIdx.length)
  const bSuffixOK = suffixOK(bSuffix)

  // console.log({ srcName, aSuffixOK, bSuffixOK, commonIdx, aSuffix, bSuffix })
  return aSuffixOK && bSuffixOK
}

function cannotHaveSidecars(f: BaseFile) {
  return (
    // sidecars don't have sidecars:
    isSidecarExt(f.ext) || inHiddenPhotoStructureDir(f)
  )
}

/**
 *  @return oldest sidecars first (so newest sidecar metadata wins, as they are
 * layered on top of eachother)
 */
export async function existingSidecars<T extends BaseFile>(
  src: T
): Promise<T[]> {
  if (cannotHaveSidecars(src)) return []
  return sortByAsync({
    name: "_existingSidecars",
    arr: src.siblings(ea => isSidecarOf(src, ea)),
    f: ea => ea.mtimeMs() // older first, see jsdoc ^^
  })
}

export function defaultSidecarExt() {
  return ensurePrefix(Settings.defaultSidecarType.valueOrDefault, ".")
}
