import { tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { later } from "../../fe/Delay"
import { ee } from "../event/EventEmitter"
import { getVideoToolDetails } from "../img/Video"
import { isDocker } from "../platform/isDocker"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { HealthCheck } from "./HealthCheck"

export const videoHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Tools",
    id: "tools-video",
    timeoutMs: ShortCommandTimeoutMs,
    pendingMsg: "Checking video tooling",
    links: [
      {
        text: "Read about PhotoStructure video support",
        url: "https://photostructure.com/getting-started/video-support/",
        icon: "movie"
      }
    ],
    warnLinks: isDocker()
      ? []
      : [
          {
            text: "Re-check for video tooling",
            icon: "refresh",
            method: "POST",
            type: "button",
            url: "/admin/recheck-tools"
          }
        ],
    rejectIsWarn: true, // if the video tooling is not installed, we don't want to block the server from starting.
    later: async () => {
      const vtd = await getVideoToolDetails()
      if (vtd.ok) {
        return {
          level: "ok",
          msg: ["Videos will be imported", tt(vtd.msg)],
          meta: {
            version: vtd.version
          }
        }
      } else {
        return {
          level: "warn",
          msg: vtd.msg
        }
      }
    }
  })
)

later(() => {
  ee().on("clearToolCache", () => videoHealthCheck.prior()?.reset())
})
