import { ExifDate, ExifDateTime, ExifTime } from "exiftool-vendored"
import { errorFromJson } from "../fe/Error"
import { lazy } from "../fe/Lazy"
import { Reviver, addRevivers } from "./JSON"
import { CapturedAt } from "./tags/CapturedAt"

const ErrorReviver: Reviver = {
  name: "Error",
  fromJSON: errorFromJson
}

export const addDefaultJsonRevivers = lazy(() =>
  addRevivers(ExifDateTime, ExifDate, ExifTime, CapturedAt, ErrorReviver)
)
