import bs, { Database } from "better-sqlite3"
import { Command } from "commander"
import { exit } from "process"
import { mkLogger } from "../core/Logger"
import { IAssetFile } from "../core/asset-file/IAssetFile"
import { endEndables } from "../core/async/Endable"
import { CLI } from "../core/cli/CLI"
import { pathToDb } from "../core/db/Schemas"
import { libraryDataDir } from "../core/dir/LibraryDirs"
import { errorToS } from "../core/error/Error"
import { PosixFile } from "../core/fs/PosixFile"
import { imageHash } from "../core/img/ImageHash"
import { Settings } from "../core/settings/Settings"
import {
  readLibrarySettings,
  readSystemSettings
} from "../core/settings/SettingsIO"
import { uri2nativePath } from "../core/uri/FileURI"
import { mapNotEmpty } from "../fe/Array"
import { blank, notBlank } from "../fe/Blank"
import { isTrue } from "../fe/Boolean"
import { stringify } from "../fe/JSON"
import { omit } from "../fe/Object"
import { ServiceNames } from "../fe/ServiceNames"
import { Library } from "./Library"
import { Service } from "./Service"
import { LogArgs } from "./cli/LogArgs"
import { knex } from "./db/Knex"
import { Queryish, toSqlQuery } from "./db/SqlQuery"
import { Asset } from "./model/Asset"
import { modelDb } from "./model/ModelDb"
import { queryStringToSql_ } from "./query/QueryToSql"
import { forceRunTagMaintenance } from "./tag/TagSql"

async function run() {
  try {
    const cli = new CLI(ServiceNames.list).add(LogArgs)
    cli.add({
      beforeParse(cmd: Command): Command {
        return cmd
          .option(
            "--reindex",
            `Rebuild the fulltext search indexes before running any query.`
          )
          .option(
            "--query <TERMS...>",
            `Apply the given query and return all matching asset file paths. Must be the last argument. Incompatible with --where.`
          )
          .option(
            "-j, --json",
            `Emit a JSON object for every asset file with "hidden" and "shown" flags`
          )
          .option(
            "--dump",
            "Emit a JSON object that includes all column values"
          )
          .option(
            "--where <WHERE>",
            "Apply a SQL WHERE clause to filter the rows returned."
          )
          .option(
            "--orderby <ORDERBY>",
            "Apply a SQL ORDER BY clause to filter the rows returned."
          )
          .option(
            "--looks-like <FILE>",
            "Find images that look like the photo or video specified by FILE. Use a full pathname."
          )
      }
    })

    const cmd = cli.parse()

    const opts = cmd.opts()

    const query = mapNotEmpty(opts.query, arr => arr.join(" "))
    const dump = isTrue(opts.dump)
    const emitJson = dump || isTrue(opts.json)
    const whereArg = opts.where

    if (query != null && notBlank(whereArg)) {
      throw new Error("--query cannot be used with --where")
    }

    const orderArg = opts.order
    let db: Database

    if (isTrue(opts.reindex)) {
      const service = new Service({ name: "list" })
      await service.ready
      Library.instanceRequired()
      await forceRunTagMaintenance()

      db = modelDb().db
    } else {
      // This is a lighter-weight startup than Library.instanceRequired()
      await readSystemSettings()
      await readLibrarySettings()

      const libraryDir = Settings.libraryDir.valueOrDefault
      if (blank(libraryDir)) {
        throw new Error(
          "No library path set. Set the " +
            Settings.libraryDir.key +
            " environment variable to the path of your PhotoStructure library."
        )
      }
      const dataDir = libraryDataDir(libraryDir)
      if (dataDir == null || (await dataDir.notExists())) {
        throw new Error(
          "The library data directory, " + dataDir + ", does not exist."
        )
      }

      const dbFile = pathToDb(dataDir, "models")

      db = new bs(dbFile.nativePath, {
        fileMustExist: true,
        readonly: false,
        timeout: Settings.dbBusyTimeoutMs.valueOrDefault
      })
    }
    const logger = mkLogger("list")

    let qb = Asset.query()
      .join("AssetFile", "AssetFile.assetId", "Asset.id")
      .join("AssetTag", "AssetTag.assetId", "Asset.id")

    qb = qb
      .select({
        assetId: "AssetFile.assetId",
        assetFileId: "AssetFile.id",
        shownFile: "AssetFile.shown",
        mountpoint: "AssetFile.mountpoint",
        uri: "AssetFile.uri",
        hidden: "Asset.hidden",
        rating: "Asset.rating"
      })
      .distinct()

    if (notBlank(opts.looksLike)) {
      const f = PosixFile.for(opts.looksLike)
      logger.info("Calculating image hash for " + f)
      const sha = await f.sha()
      const ih = await imageHash(f)
      if (ih == null) {
        throw new Error("Failed to generate image hash for " + f)
      }
      logger.info("Looking for assets with matching meanHash or SHA", {
        meanHash: ih.meanHash,
        SHA: sha
      })

      qb = qb.where(q =>
        q.where("AssetFile.meanHash", ih.meanHash).orWhere("AssetFile.sha", sha)
      )
    }

    if (dump) {
      qb = qb.select(knex().raw("AssetFile.*"), knex().raw("Asset.*"))
    }

    if (notBlank(whereArg)) {
      qb = qb.andWhere(knex().raw(whereArg))
    } else {
      qb = Asset.shownUnhidden(qb)
    }

    const orderBy = notBlank(orderArg) ? orderArg : "uri"

    const q: Queryish =
      query != null ? queryStringToSql_(query, qb).orderByRaw(orderBy) : qb

    const sq = toSqlQuery(q)
    logger.info("Running SQL query...", {
      sq
    })

    const start = Date.now()
    const result = db
      .prepare(sq.sql)
      .iterate(sq.bindings) as IterableIterator<IAssetFile>
    logger.info("query took " + (Date.now() - start) + "ms")

    for (const o of result) {
      const nativePath = await uri2nativePath(o.uri, o.mountpoint)
      if (emitJson) {
        console.log(stringify({ path: nativePath, ...omit(o, "id") }))
      } else {
        console.log(nativePath ?? o.uri)
      }
    }
  } catch (err) {
    console.log("Failed: " + errorToS(err))
    exit(1)
  } finally {
    await endEndables()
    exit(0)
  }
}

void run()
