const logger = lazy(() => mkLogger("img.vlc"))

export function vlcVersionDescription() {
  return thenMapOr(
    vlcInfo().catch(() => undefined),
    vlc => "version " + vlc.version,
    () => "(not found)"
  )
}

const VersionRe = /VLC (?:version|media player) ([0-9\.]+)/i

export interface VLCInfo {
  path?: string
  version?: string
}

// See: https://superuser.com/questions/947116/how-do-i-use-vlc-command-line-in-windows-batch-line-without-showing-gui

const DefaultVlcArgs = [
  "--intf=dummy", // don't render the interface
  "--vout=dummy", // don't render the video
  "--aout=dummy", // don't render the audio
  "--quiet"
]

later(() => {
  ee().on("clearCache", () => _vlcInfo.unset())
  ee().on("clearToolCache", () => _vlcInfo.unset())
})

export async function vlcInfo() {
  return (await _vlcInfo()) ?? _vlcInfo.refresh()
}

export async function isVlcSupported() {
  // 20200320: The raspbian/ARM port of VLC doesn't work correctly.
  return !isRaspberryPi() && notBlank((await vlcInfo())?.version)
}

export function vlcPath() {
  return thenMap(vlcInfo(), ea => (ea.version == null ? undefined : ea.path))
}

async function versionPosix(path: string): PromiseMaybe<string> {
  return thenMap(
    stdoutResult_(
      path,
      // VLC on linux and mac don't use `--dummy-quiet`.
      [...DefaultVlcArgs, "--version"],
      {
        timeoutMs: commandTimeoutMs(),
        // VLC burps "VLC media player 3.0.6 Vetinari (revision
        // 3.0.6-0-g5803e85)" to stderr. SRSLY WTH. STDERR.
        ignoreStderr: true,
        quiet: true,
        isIgnorableError: () => true
      }
    ),
    result =>
      opt(result.result)
        .filter(notBlank)
        .map(ea => ea.trim())
        .flatMap(ea => VersionRe.exec(ea))
        .flatMap(ea => ea[1])
        .get()
  )
}

function versionWin(path: string): PromiseMaybe<string> {
  // Windows VLC --version pops up a terminal no matter what options you give
  // it. YAY
  return thenMap(
    PowerShell.instance()
      .execute(
        `(Get-Item -path ${pwshQuote(path)}).VersionInfo.FileVersion`,
        ea => ea
      )
      .catch(err => {
        logger().warn("Failed to run vlcInfoWin: " + path + ": " + err)
        return undefined
      }),
    result => denull(result.trim())
  )
}

const _vlcInfo = lazy<PromiseMaybe<VLCInfo>>(async () => {
  const paths: (string | PosixFile)[] = []

  // Always add "vlc" in PATH if we're on linux:
  if (Settings.vlcPath.hasValue() || isLinux) {
    // we're pushing the string here, rather than a PosixFile, so we try even if
    // the path doesn't exist (as it may be in the PATH)
    paths.push(Settings.vlcPath.valueOrDefault)
  }

  function add(...arr: string[]) {
    paths.push(PosixFile.for(join(...arr)))
  }

  if (isMac) {
    const subpath = "/Applications/VLC.app/Contents/MacOS/VLC"
    mapNotBlank(getEnv("HOME"), ea => add(ea + subpath))
    add(subpath)
  }
  if (isWin) {
    const subpath = ["VideoLAN", "VLC", "vlc.exe"]
    mapNotBlank(getEnv("LOCALAPPDATA"), ea => {
      add(ea, "Apps", ...subpath)
    })
    mapNotBlank(getEnv("ProgramFiles"), ea => {
      add(ea, ...subpath)
    })
    mapNotBlank(getEnv("ProgramFiles(x86)"), ea => {
      add(ea, ...subpath)
    })
  }
  if (isLinux) {
    // backstop if PATH isn't set:
    add("/usr/bin/vlc")
  }

  for (const pathOrFile of paths) {
    try {
      if (
        pathOrFile instanceof PosixFile &&
        (await pathOrFile.clear().notExists())
      ) {
        continue
      }
      const path = pathOrFile.toString()
      const version = await (isWin ? versionWin(path) : versionPosix(path))
      if (blank(version)) {
        logger().warn("vlcInfo(): no VLC version found for " + pathOrFile)
        continue
      } else {
        return logger().tap({ msg: "vlcInfo()", result: { path, version } })
      }
    } catch (err) {
      logger().warn("vlcInfo(): err:" + err, { path: pathOrFile })
    }
  }
  return
})

// https://wiki.videolan.org/How_to_create_thumbnails/
export async function vlcFrame(args: {
  src: PosixFile
  dest: PosixFile
  startAtSec?: number
  width?: number
  height?: number
}): PromiseMaybe<StdoutResult> {
  const vlc = await vlcPath()
  if (blank(vlc)) {
    return logger().throw("vlcFrame(): empty vlcPath", {
      ...args,
      ignorable: true
    })
  }
  const format = stripPrefix(args.dest.ext, ".").toLowerCase()
  if (!["png", "jpg"].includes(format)) {
    return logger().throw(
      "vlcFrame(): Invalid destination extension" + NonRetriableErrorFlag,
      { format, args }
    )
  }
  const startTime = args.startAtSec ?? 0
  const stopTime = startTime + 1
  const result = await stdoutResult_(
    vlc,
    compact([
      // BEWARE! VLC NEEDS THIS ORDER OF ARGUMENTS TO WORK! SRSLYWTF
      ...DefaultVlcArgs,
      "--avcodec-hw=none", // https://forum.videolan.org/viewtopic.php?f=13&t=129067&p=486082&hilit=snapshot#p433025
      "--video-filter=scene", // enable post-processing to make image quality better
      "--scene-format=" + format,
      "--scene-replace",
      // Ratio of images to record. 3 means that one image out of three is
      // recorded. This is ridiculously large so we only get one frame:
      "--scene-ratio=500",

      // VLC doesn't quite do dimensions correctly, but as of 20191205, it's
      // better than what we have otherwise, so this is commented out.
      // map(args.width, ea => "--scene-width=" + ea),
      // map(args.height, ea => "--scene-height=" + ea),

      "--scene-path=" + args.dest.dir,
      "--scene-prefix=" + args.dest.name,
      "--start-time=" + startTime.toFixed(1), // this makes the poster image match the start of the video.
      "--stop-time=" + stopTime.toFixed(1),
      args.src.nativePath,
      "vlc://quit"
    ]),
    {
      timeoutMs: minuteMs,
      ignoreStderr: true
    }
  )
  // console.log("vlcFrame()", { result })
  // if (gt0(result.pid)) {
  //   if (!(await until(() => pidNotExists(result.pid), 7 * secondMs))) {
  //     await kill(result.pid, true)
  //   }
  // }
  args.dest.clear()
  if (gt0(result.code)) {
    throw new Error("vlc failed: " + stringify(result))
  }
  return result
}

export async function vlcTranscode_(args: {
  src: PosixFile
  dest: PosixFile
  width: Maybe<number>
  height: Maybe<number>
  videoBitrateKbps?: number
  timeoutMs: number
}) {
  const vlc = await vlcPath()
  if (blank(vlc)) {
    throw new Error(
      "vlc.transcode(): VLC is not installed, cannot transcode " + args.src
    )
  }

  // Enable debug logging within VLC, restart, then run a convert job. These
  // options will be in the log.

  // mp4 is the most compatible format, according to https://en.wikipedia.org/wiki/HTML5_video

  // AAC encoding seems to be required for audio playback across different
  // source video formats.

  // $ ffmpeg -hide_banner -f lavfi -i nullsrc -c:v libx264 -preset faster -f mp4 -

  // options: cabac=1 ref=2 deblock=1:0:0 analyse=0x3:0x113 me=hex subme=4 psy=1
  // psy_rd=1.00:0.00 mixed_ref=0 me_range=16 chroma_me=1 trellis=1 8x8dct=1
  // cqm=0 deadzone=21,11 fast_pskip=1 chroma_qp_offset=0 threads=6
  // lookahead_threads=1 sliced_threads=0 nr=0 decimate=1 interlaced=0
  // bluray_compat=0 constrained_intra=0 bframes=3 b_pyramid=2 b_adapt=1
  // b_bias=0 direct=1 weightb=1 open_gop=0 weightp=1 keyint=250 keyint_min=25
  // scenecut=40 intra_refresh=0 rc_lookahead=20 rc=crf mbtree=1 crf=23.0
  // qcomp=0.60 qpmin=0 qpmax=69 qpstep=4 ip_ratio=1.40 aq=1:1.00

  // mrm@i3:~$ ffmpeg -hide_banner -f lavfi -i nullsrc -c:v libx264 -preset slow
  // -f mp4 -

  // options: cabac=1 ref=5 deblock=1:0:0 analyse=0x3:0x113 me=umh subme=8 psy=1
  // psy_rd=1.00:0.00 mixed_ref=1 me_range=16 chroma_me=1 trellis=1 8x8dct=1
  // cqm=0 deadzone=21,11 fast_pskip=1 chroma_qp_offset=-2 threads=6
  // lookahead_threads=1 sliced_threads=0 nr=0 decimate=1 interlaced=0
  // bluray_compat=0 constrained_intra=0 bframes=3 b_pyramid=2 b_adapt=2
  // b_bias=0 direct=3 weightb=1 open_gop=0 weightp=2 keyint=250 keyint_min=25
  // scenecut=40 intra_refresh=0 rc_lookahead=50 rc=crf mbtree=1 crf=23.0
  // qcomp=0.60 qpmin=0 qpmax=69 qpstep=4 ip_ratio=1.40 aq=1:1.00

  const tcOpts = compact([
    "vcodec=h264",
    mapGt0(args.videoBitrateKbps, ea => "vb=" + ea),
    mapGt0(args.width, w => "width=" + Math.floor(w)),
    mapGt0(args.height, h => "height=" + Math.floor(h)),
    // These are the "faster" options that differ from the "slower" presets:
    "venc=x264{ref=2:me=hex:mixed_ref=0:chroma_qp_offset=0:b_adapt=1:direct=1:weightp=1:rc_lookahead=20}",
    // https://support.plex.tv/articles/200250347-transcoder/
    // "faster encoding": (basically the same speed as hq)
    // "venc=x264{subme=0:me_range=4:rc_lookahead=10:me=dia:no_chroma_me:8x8dct=0:partitions=none}",
    // "hq encoding":
    // "venc=x264{subme=0:me_range=4:rc_lookahead=10:me=hex:8x8dct=0:partitions=none}",
    // "make my cpu hurt":
    // "venc=x264{subme=0:me_range=4:rc_lookahead=10:me=hex:8x8dct=1}",
    "acodec=mp4a",
    "ab=128",
    "channels=2",
    "samplerate=44100",
    "scodec=none",
    "deinterlace"
  ]).join(",")

  const stdOpts = [
    "access=file{overwrite}",
    "mux=mp4",
    "dst=" + args.dest.base
  ].join(",")

  return stdoutResult_(
    vlc,
    [
      ...DefaultVlcArgs,
      "--no-repeat",
      "--no-loop",
      args.src.fileuri(),
      `--sout=#transcode{${tcOpts}}:standard{${stdOpts}}`,
      "vlc://quit"
    ],
    {
      cwd: args.dest.dir,
      timeoutMs: args.timeoutMs,
      ignoreStderr: true
    }
  )
}
