import { sort } from "./Array"
import { isBoolean } from "./Boolean"
import { isFunction } from "./isFunction"
import { Maybe } from "./MaybeTypes"
import { keysWithDefinedValues } from "./Object"
import { isPrimitive } from "./Primitive"
import { eqlStrings, isString } from "./String"

export function isEqlSimple(a: any, b: any): boolean | undefined {
  // same pointer or primitive value is eql
  if (a === b) return true

  // undefined != null
  if (a == null || b == null) return false

  // a and b must be the same type
  if (typeof a !== typeof b) return false

  // typeof's match, so if either is a string, both are
  if (isString(a)) {
    return eqlStrings(a, b)
  }

  if (isPrimitive(a)) {
    return a === b
  }

  return undefined
}

const DefaultMaxDepth = 6

export function eql(a: any, b: any, maxDepth = DefaultMaxDepth): boolean {
  if (maxDepth < 0) return false

  const simpleEql = isEqlSimple(a, b)
  if (simpleEql != null) return simpleEql

  // typeof [] === 'object' (!!!!), so don't test for isObject before isArray!
  if (Array.isArray(a) || Array.isArray(b)) {
    return isEqlArray(a, b, maxDepth) ?? false
  }

  if (isFunction(a) && isFunction(b)) {
    // toString() is the implementation
    return a.toString() === b.toString()
  }

  if (typeof a === "object" && typeof b === "object") {
    return isEqlObject(a, b)
  }
  return false
}

export function isEqlArray(
  a: any,
  b: any,
  maxDepth = DefaultMaxDepth
): boolean | undefined {
  if (!Array.isArray(a) || !Array.isArray(b)) return undefined
  if (a.length !== b.length) return false
  for (let i = 0; i < a.length; i++) {
    if (!eql(a[i], b[i], maxDepth - 1)) return false
  }
  return true
}

export function dateComparator(a: any, b: any): undefined | boolean {
  if (a instanceof Date && b instanceof Date) {
    return a.getTime() === b.getTime()
  }
  return undefined
}

export type Comparator = (a: any, b: any) => undefined | boolean

const objComparators: Comparator[] = [dateComparator]
let fieldComparators: Maybe<Map<string, Comparator>>

export function pushObjComparator(c: Comparator) {
  objComparators.push(c)
}

export function unshiftObjComparator(c: Comparator) {
  objComparators.unshift(c)
}

export function setFieldComparator(fieldName: string, c: Comparator) {
  ;(fieldComparators ??= new Map()).set(fieldName, c)
}

export function fieldEql(
  fieldName: string,
  a: any,
  b: any,
  maxDepth = DefaultMaxDepth
): boolean {
  return fieldComparators?.get(fieldName)?.(a, b) ?? eql(a, b, maxDepth - 1)
}

export function isEqlObject(
  a: Record<string, unknown>,
  b: Record<string, unknown>,
  maxDepth = DefaultMaxDepth
): boolean {
  if (maxDepth < 0 || typeof a !== "object" || typeof b !== "object")
    return false
  if (a === b) return true

  for (const c of objComparators) {
    const result = c(a, b)
    if (result != null) return result
  }

  if (isFunction(a.eql)) {
    const r = a.eql(b)
    if (isBoolean(r)) return r
  }

  if (isFunction(b.eql)) {
    const r = b.eql(a)
    if (isBoolean(r)) return r
  }

  const aObj = isFunction(a.toJSON) ? a.toJSON() : a
  const bObj = isFunction(b.toJSON) ? b.toJSON() : b

  const e = isEqlSimple(aObj, bObj)
  if (e != null) return e

  // the order of keys
  const aKeys = sort(keysWithDefinedValues(aObj))
  const bKeys = sort(keysWithDefinedValues(bObj))

  if (true !== isEqlArray(aKeys, bKeys, maxDepth)) return false

  for (const key of aKeys) {
    if (fieldEql(key, aObj[key], bObj[key], maxDepth - 1) === false) {
      return false
    }
  }
  return true
}
