const logger = lazy(() => mkLogger("date.FuzzyDateParser"))

export interface FuzzyDateParserOptions {
  fuzzyDateParsing?: boolean
  fuzzyYearParsing?: boolean
}

export function extractDateFromPath(
  path: string[]
): Maybe<FuzzyDate | ExifDateTime> {
  return FuzzyDateParser.instance().extractDateFromPath(path)
}

export function parseYMD(s: string): Maybe<FuzzyDate | ExifDateTime> {
  return FuzzyDateParser.instance().parseYMD(s)
}

export function parseFuzzyDate(s: string): Maybe<FuzzyDate> {
  return FuzzyDateParser.instance().parse(s)
}

// We're using literal RegExps here so syntax highlighting can help find
// issues:

// negative numeric lookbehind to prevent "1992 AUG 1971" from being parsed as
// "2 AUG 1971":
export const nonNumericLookbackRE = /(?<!\d)/

export const yearRE = /(?<year>(?:19|20)[0-9]{2})/ // 1975, 2014
export const yearishRE = /(?<year>(?:19|20)?[0-9]{2})/ // 1975, 2014

export const monthRE = /(?<month>[0-3][0-9])/ // < requires 0-padded month
export const monthishRE = /(?<month>[0-3]?[0-9])/

export const dayRE = /(?<day>[1-3][0-9]|0[1-9])/ // < requires 0-padded day
export const dayishRE = /(?<day>[1-3][0-9]|0?[1-9])/

export const hourRE = /(?<hour>1[0-9]|2[0-3]|0[0-9])/

export const minuteRE = /(?<minute>[0-5][0-9])/

export const secondRE = /(?<second>[0-5][0-9])/

export const seps = /([-\.\,:_/ |])/
export const optSeps = new RegExp(seps.source + "?")

export const subsecRE = /(?<subsec>\.[0-9]+)?/

export function dateTimeBetween(a: DateTime, b: DateTime): DateTime {
  // When would this ever be invalid (and therefor null)?

  // Assuming it can't ever be invalid, we're bang-ing the end. Yeah. We're
  // end bangers over here. JOIN US WE KNOW YOU ARE BANGCURIOUS
  return a.until(b).divideEqually(2)[0].end!
}

export function agoMs(dated: Maybe<Dated>): Maybe<number> {
  return mapNumeric(datedToMillis(dated), ea => Date.now() - ea)
}

export const ISO_YMD_RE = concatRegexp([
  /^/,
  yearRE,
  RegExpOptional.from(/-/, monthRE, RegExpOptional.from(/-/, dayRE)),
  /$/
])

export const ISO_YMD_LAX_RE = concatRegexp([
  /^/,
  yearishRE,
  RegExpOptional.from(/-/, monthishRE, RegExpOptional.from(/-/, dayishRE)),
  /$/
])

export function parseDateTime(
  s: Maybe<string> | Maybe<string>[],
  defaultZone?: string
): Maybe<DateTime> {
  const d = parseDated({
    input: s,
    defaultZone,
    descPredicate: ea => ea.match(/DateTime/i) != null
  })

  return isDateTime(d) ? d : isExifDateTime(d) ? d.toDateTime() : undefined
}

const ZoneSuffixes = [
  { fmt: "ZZ", zone: undefined },
  { fmt: " ZZ", zone: undefined },
  { fmt: "'Z'", zone: "UTC" },
  { fmt: "", zone: UnsetZone }
]

/**
 * Parses ISO, EXIF, and screenshot-formatted dates.
 */
export function parseDated({
  input,
  defaultZone,
  descPredicate = () => true
}: {
  input: MaybeNull<Dated | string | Maybe<string>[]>
  defaultZone?: string
  descPredicate?: (desc: string) => boolean
}): Maybe<Dated> {
  if (input == null) return
  if (isDated(input)) return input
  const arr = uniq(compactBlanks(toA(input)).map(stripDSC))
  if (arr.length === 0) return
  for (const { desc, f } of [
    // date interval (must be first):
    {
      desc: "DateInterval.fromISO",
      f: (ea: string) => DateInterval.fromISO(ea)
    },

    // date and time:
    {
      desc: "parseExifDateTime",
      // this calls ExifDateTime.fromISO and ExifDateTime.fromEXIF
      f: (ea: string) => parseExifDateTime(ea, defaultZone)
    },

    ...flatten(
      ZoneSuffixes.map(({ fmt, zone }) =>
        Settings.extraDateTimeFormats.values.map(format => ({
          desc: `parseExifDateTimeFormat(${format})`,
          f: (ea: string) =>
            parseExifDateTimeFormat({
              input: ea,
              format: format + fmt,
              defaultZone: zone
            })
        }))
      )
    ),

    { desc: "ExifDateTime.fromRFC2822", f: parseExifDateTimeFromRFC2822 },

    // HEY FUTURE ME DO NOT RE-ADD JS DATE PARSING it forces everything to UTC

    // just dates:

    { desc: "ExifDate.fromEXIF", f: (ea: string) => ExifDate.fromEXIF(ea) },
    {
      desc: "fuzzyDate.parseYMD",
      f: (ea: string) => FuzzyDateParser.instance().parseYMD(ea)
    }
  ]) {
    if (descPredicate(desc)) {
      for (const s of arr) {
        const result = f(s)
        if (result == null || !isValidDate(result)) continue

        // Don't set zone here: that was the parser's job.

        return logger().tap({
          msg: "parseDated()",
          result,
          meta: {
            s,
            desc,
            defaultZone
          }
        })
      }
    }
  }
  return
}

export class FuzzyDateParser {
  static readonly instance = lazy(() => new FuzzyDateParser())

  private readonly ymdParsers: Parser[] = []
  private readonly ymParsers: Parser[] = []
  private readonly yParsers: Parser[] = []
  private readonly allParsers: Parser[] = []

  constructor(readonly opts: FuzzyDateParserOptions = {}) {
    if (opts.fuzzyDateParsing == null) {
      Settings.fuzzyDateParsing.watchLater(() => this.#setup())
    }
    if (opts.fuzzyYearParsing == null) {
      Settings.fuzzyYearParsing.watchLater(() => this.#setup())
    }
    this.#setup()
  }

  get fuzzyDateParsing() {
    return (
      this.opts.fuzzyDateParsing ?? Settings.fuzzyDateParsing.valueOrDefault
    )
  }

  get fuzzyYearParsing() {
    return (
      this.opts.fuzzyYearParsing ?? Settings.fuzzyYearParsing.valueOrDefault
    )
  }

  parse(input: string): Maybe<FuzzyDate> {
    return first(this.allParsers, p => p.apply(input))
  }

  parseYMD(input: string): Maybe<FuzzyDate> {
    return first(this.ymdParsers, p => p.apply(input))
  }

  #addParser(arr: (RegExp | string | RegExpEscaped)[]) {
    const p = new Parser(arr)
    if (p.hasDay) {
      this.ymdParsers.push(p)
    } else if (p.hasMonth) {
      if (this.fuzzyDateParsing) {
        this.ymParsers.push(p)
      }
    } else if (this.fuzzyYearParsing) {
      this.yParsers.push(p)
    }
  }

  #setup() {
    try {
      this.ymdParsers.length = 0
      this.ymParsers.length = 0
      this.yParsers.length = 0
      this.allParsers.length = 0

      const allMonthsRE = new RegExpEscaped(
        "(?<monthname>" + monthNames().map(escapeRegExp).join("|") + ")"
      )

      // Very conservative/restrictive patterns to guard against mmddyy or ddmmyy:
      // ISO 8601: 1999-11-11 (the /\2/ ensures consistent seps).
      // Seps are required if month and day aren't padded:
      this.#addParser([yearRE, seps, monthishRE, /\2/, dayishRE])

      // If month and day are padded, seps are optional:
      this.#addParser([yearRE, optSeps, monthRE, /\2/, dayRE])

      // 2011:Feb:6
      this.#addParser([yearRE, optSeps, allMonthsRE, /\2/, dayishRE])

      // August 6, 1994
      this.#addParser([allMonthsRE, optSeps, dayishRE, /,?\2/, yearRE])

      // 6 August 1994
      this.#addParser([
        nonNumericLookbackRE,
        dayishRE,
        optSeps,
        allMonthsRE,
        /,?\2/,
        yearRE
      ])

      // The following are only year + month (no date):
      // 1994-08
      this.#addParser([yearRE, seps, monthishRE])

      // Sept 1994 or September 1994
      this.#addParser([allMonthsRE, seps, yearRE])

      // 2001 August or 2001 AUG
      this.#addParser([yearRE, seps, allMonthsRE])

      this.#addParser([nonNumericLookbackRE, yearRE])

      this.allParsers.push(
        // order matters! We want the most specific parsers first:
        ...this.ymdParsers,
        ...this.ymParsers,
        ...this.yParsers
      )
    } catch (e) {
      console.error(e)
    }
  }

  extractDateFromPath(path: string[]): Maybe<FuzzyDate | ExifDateTime> {
    if (!Settings.usePathsToInferDates.valueOrDefault) return

    // https://en.wikipedia.org/wiki/Design_rule_for_Camera_File_system
    ignorableSubpaths.forEach(ea => {
      if (startsWith(path, ea)) {
        path.splice(0, ea.length)
      }
    })
    path = path.filter(
      // If it's a camera-provided serial number, ignore that part of the path.
      ea => notBlank(ea) && path[0].match(ignorableDCF) == null
    )
    // Don't modify path!
    const reversePath = [...path].reverse()

    return firstThunk(
      // First try to find a YMD in one of the path elements individually:
      () => first(reversePath, ea => this.parseYMD(ea)),
      // Then try to find YMD by joining the paths (reverse first, so nearer
      // datestamps get picked first)
      () => this.parseYMD(reversePath.slice(0, 4).join(" ")),
      // is it a path like 2019/08/01?
      () => this.parseYMD(path.slice(0, 4).join(" ")),
      // the ymParsers will be empty if fuzzy date parsing is off
      () =>
        first(this.ymParsers, parser =>
          first(reversePath, ea => parser.apply(ea))
        ),
      () => first(this.ymParsers, parser => parser.apply(path.join(" "))),
      () =>
        first(this.yParsers, parser =>
          first(reversePath.slice(1), ea => parser.apply(ea))
        )
    )
  }
}

class Parser {
  readonly regex: RegExp
  constructor(arr: Maybe<RegExp | string | RegExpEscaped>[]) {
    // We always add a negative lookahead to the end of the regex to avoid
    // matching against long numeric strings:
    this.regex = concatRegexp([...arr, /(?:$|[^\d])/], "i")
  }

  get source() {
    return this.regex.source
  }

  get hasDay() {
    return this.source.includes("(?<day>")
  }

  get hasMonth() {
    return (
      this.source.includes("(?<month>") || this.source.includes("(?<monthname>")
    )
  }

  apply(s: string) {
    const rec = this.regex.exec(s)?.groups
    if (rec == null) return
    return FuzzyDate.for({
      year: toInt(rec.year),
      month: toInt(rec.month) ?? monthName2index(rec.monthname),
      day: toInt(rec.day)
    })
  }
}

const ignorableDCF =
  /^((DCIM)|(DSC[_0F]?|IMG[-_]|GOPRO|MOV[-_]|MVI[-_]|P_?)\d+)$/i
