import { tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { later } from "../../fe/Delay"
import { map } from "../../fe/Maybe"
import { ee } from "../event/EventEmitter"
import { getHeifSupportDetails } from "../img/Heif"
import { HealthCheck } from "./HealthCheck"

export const heifHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Tools",
    id: "tools-heif",
    pendingMsg: "Checking HEIF/HEIC tooling",
    links: [
      {
        text: "Read about PhotoStructure HEIF support",
        url: "https://photostructure.com/getting-started/heif-support/",
        icon: "camera_alt"
      }
    ],

    // if the HEIF tooling is not installed, we don't want to block the server
    // from starting:
    rejectIsWarn: true,

    later: async () => {
      const vtd = await getHeifSupportDetails()
      if (vtd.ok) {
        return {
          level: "ok",
          msg: [
            "HEIF images will be imported",
            map(vtd.version, tt),
            map(vtd.path, tt)
          ]
        }
      } else {
        return {
          level: "warn",
          msg: ["HEIF images will not be imported", vtd.msg]
        }
      }
    }
  })
)

later(() => {
  ee().on("clearToolCache", () => heifHealthCheck.prior()?.reset())
})
