import { errorToJson } from "./Error"
import { denull } from "./Maybe"
import { MaybeNull } from "./MaybeTypes"

export type Json =
  | string
  | number
  | boolean
  | null
  | undefined
  | { [property: string]: Json }
  | Json[]

export type DatedJson =
  | string
  | number
  | boolean
  | null
  | undefined
  | Date
  | { [property: string]: DatedJson }
  | DatedJson[]

/**
 * Work-alike to JSON.stringify, except that circular dependencies are replaced
 * with a string reference.
 */
export function stringify(
  ea: any,
  replacer?: (this: any, key: string, value: any) => any,
  space?: string | number,
  cycleReplacer?: (this: any, key: string, value: any) => any
): string {
  return JSON.stringify(ea, serializer(replacer, cycleReplacer), denull(space))
}

function serializer(
  replacer?: MaybeNull<(this: any, key: string, value: any) => any>,
  cycleReplacer?: MaybeNull<(this: any, key: string, value: any) => any>
): (this: any, key: string, value: any) => any {
  const stack: any[] = []
  const keys: any[] = []

  const cr =
    cycleReplacer ??
    ((_key: string, value: any) =>
      stack[0] === value
        ? "[Circular ~]"
        : "[Circular ~." + keys.slice(0, stack.indexOf(value)).join(".") + "]")

  /**
   * The object in which the key was found is provided as the replacer's `this`
   * parameter.
   */
  return function (this: any, key: string, value: any) {
    if (stack.length > 0) {
      const thisPos = stack.indexOf(this)
      if (thisPos >= 0) {
        stack.splice(thisPos + 1)
        keys.splice(thisPos, Infinity, key)
      } else {
        stack.push(this)
        keys.push(key)
      }
      if (stack.indexOf(value) >= 0) {
        value = cr.call(this, key, value)
      }
    } else {
      stack.push(value)
    }
    return value instanceof Error
      ? errorToJson(value)
      : replacer != null
      ? replacer.call(this, key, value)
      : value
  }
}
