import { networkInterfaces } from "os"
import { compactBlanks, uniq } from "../../fe/Array"
import { blank, notBlank } from "../../fe/Blank"
import { minuteMs } from "../../fe/Date"
import { flatten } from "../../fe/Flatten"
import { lazy } from "../../fe/Lazy"
import { values } from "../../fe/Object"
import { mkLogger } from "../Logger"
import { stripPrefix } from "../String"

export const myIpAddresses = lazy(
  () =>
    uniq(
      compactBlanks(flatten(values(networkInterfaces())).map(ea => ea.address))
    ),
  minuteMs
)

export function myExternalIpAddresses() {
  return myIpAddresses().filter(ea => !isLoopback(ea))
}

export function myExternalIp4Addresses() {
  return myExternalIpAddresses().filter(ea => !ea.includes(":"))
}

const logger = lazy(() => mkLogger("net.ip"))

const loopbackPatterns = [
  /^127\.\d{1,3}.\d{1,3}.\d{1,3}$/,
  /^(?:::ffff:)?127\.\d{1,3}.\d{1,3}.\d{1,3}$/i,
  /^::1$/
]

export function isLoopback(address: string) {
  return (
    notBlank(address) && loopbackPatterns.some(re => re.exec(address) != null)
  )
}

export function isLocalhost(address: string) {
  return logger().tap({
    msg: "isLocalhost",
    level: "info",
    result: blank(address)
      ? false
      : myIpAddresses().includes(address) ||
        myIpAddresses().includes(stripPrefix(address, "::ffff:")) ||
        isLoopback(address),
    meta: { address }
  })
}
