const watchSettings = lazy(() => {
  Settings.excludeGlobsAdd.watchLater(() => excludeBundles.unset())
  Settings.excludeGlobsOmit.watchLater(() => excludeBundles.unset())
})

export function resetGlobs() {
  Settings.excludeGlobsAdd.unset()
  Settings.excludeGlobsOmit.unset()
}

