export const excludeGlobs = lazy(() => {
  const result = new Map<string, Glob>()
  function addGlob(glob: Glob) {
    if (glob != null && glob.valid) {
      const k = glob.glob.toLowerCase().trim()
      if (result.has(k)) {
        logger().warn("Duplicate glob:", { glob })
      }
      result.set(k, glob)
    }
  }

  function omitGlob(glob: string) {
    const k = glob.toLowerCase().trim()
    if (!result.has(k)) {
      logger().warn(
        "Failed to find and remove glob specified in PS_EXCLUDE_GLOBS_OMIT",
        { glob }
      )
    } else {
      result.delete(k)
    }
  }

  watchSettings()
  defaultExcludeGlobs().forEach(addGlob)
  for (const ea of Settings.excludeGlobsAdd.values) {
    addGlob(new Glob(ea, "(user-provided)"))
  }
  for (const glob of Settings.excludeGlobsOmit.values) {
    omitGlob(glob)
  }
  const arr = sortBy(result.values(), ea =>
    compactBlanks(ea.glob.toLowerCase().split("/"))
  )
  const [dir, file] = partition(arr, ea => ea.glob.endsWith("/"))

  return { dir, file }
})

export const excludeBundles = lazy(() => {
  const g = excludeGlobs()
  return {
    dir: [
      ...defaultDirPredicates(),
      mergeObjects(...g.dir.map(ea => ea.bundle))
    ] as PredicateBundle<SimpleFileOrString>,
    file: [
      ...defaultFilePredicates(),
      mergeObjects(...g.file.map(ea => ea.bundle))
    ] as SyncPredicateBundle<SimpleFileOrString>
  }
})

const ifDisabled: SyncPredicateBundle<SimpleFileOrString> = [
  { disableIgnorableFilters: () => true }
]
const ifBlank: SyncPredicateBundle<SimpleFileOrString> = [
  { notBlank: () => false }
]

export function notExcludedFilePredicates(
  file: SimpleFileOrString
): SyncPredicateBundle<SimpleFileOrString> {
  return Settings.disableIgnorableFilters.valueOrDefault
    ? ifDisabled
    : blank(file)
    ? ifBlank
    : excludeBundles().file
}

export function whyExcludedFile(f: SimpleFileOrString): Maybe<string> {
  return SyncPredicates.whyRejected(f, ...notExcludedFilePredicates(f))
}

export function isExcludedFile(f: SimpleFileOrString): boolean {
  return null != whyExcludedFile(f)
}

export function notExcludedDirPredicates(
  dir: SimpleFileOrString
): PredicateBundle<SimpleFileOrString> {
  if (Settings.disableIgnorableFilters.valueOrDefault) return ifDisabled
  if (blank(dir)) return ifBlank
  return excludeBundles().dir
}

export async function whyExcludedDirectory(
  dir: SimpleFileOrString
): PromiseMaybe<string> {
  return Predicates.whyRejected(dir, ...notExcludedDirPredicates(dir))
}

export async function isExcludedDirectory(
  f: SimpleFileOrString
): Promise<boolean> {
  return null != (await whyExcludedDirectory(f))
}

export async function whyExcludedDirectoryRecursive(
  dir: SimpleFile
): PromiseMaybe<string> {
  return firstSelfOrAncestor(dir, whyExcludedDirectory)
}

function notSymlinkLoop(dir: SimpleFileOrString): boolean {
  return !seemsLikeSymlinkLoop(dir)
}

function notNoMedia(dir: SimpleFileOrString) {
  return thenNot(hasNoMedia(dir))
}

function notHidden(dir: SimpleFileOrString) {
  return thenNot(isHidden(dir))
}

export function notInHiddenPhotoStructureDir(dir: SimpleFileOrString) {
  return !inHiddenPhotoStructureDir(dir)
}
