import { notBlank } from "../fe/Blank"
import { Maybe } from "../fe/MaybeTypes"
import { Obj } from "../fe/Object"
import { bufferToString } from "./Buffer"

export function parseMaybe<T = Obj>(s: Maybe<string | Buffer>): Maybe<T> {
  // perf: unrolled
  const str = s == null ? undefined : bufferToString(s)
  try {
    if (notBlank(str)) {
      return parseJSON(str)
    }
  } catch {
    //
  }
  return
}

export function mapParsed<T>(s: string, f: (o: Obj) => T): Maybe<T> {
  try {
    if (notBlank(s)) {
      return f(JSON.parse(s))
    }
  } catch {
    //
  }
  return
}

export interface Reviver {
  name: string
  fromJSON(ea: any): any
}

const Revivers = new Map<string, Reviver>()

export function addRevivers(...arr: Reviver[]) {
  for (const ea of arr) {
    Revivers.set(ea.name, ea)
  }
}

export function parseJSON(s: string) {
  return JSON.parse(
    s,
    (_key, value) =>
      (value?._ctor != null
        ? Revivers.get(value._ctor)?.fromJSON(value)
        : undefined) ?? value
  )
}
