import { compact } from "../../fe/Array"
import { Link } from "../../fe/HealthChecks"
import { IconName } from "../../fe/IconName"
import { Maybe } from "../../fe/MaybeTypes"
import { isDocker } from "../platform/isDocker"
import { libraryHasSettings } from "../settings/SettingsIO"

export function ifNoLibraryLink(): Link {
  return isDocker()
    ? {
        text: "Read about PhotoStructure docker volumes",
        url: "https://photostructure.com/go/docker-volumes/",
        icon: "dns" as IconName
      }
    : {
        text: `What's a "PhotoStructure library?"`,
        icon: "logo" as IconName,
        url: "https://photostructure.com/faq/library/"
      }
}

export function setupLibraryButtons(): Maybe<Link[]> {
  return isDocker()
    ? undefined
    : [
        {
          text: "Set up a new library",
          type: "button",
          method: "GET",
          url: "/welcome",
          icon: "rocket_launch"
        }
      ]
}

export function restartResetOrShutdownButtons(): Link[] {
  return compact([
    libraryHasSettings()
      ? {
          text: "Try again",
          title: "Restart the current library",
          type: "button",
          method: "POST",
          url: "/admin/restart",
          icon: "refresh"
        }
      : undefined,
    {
      text: "Change PhotoStructure library",
      title: "Create a new library or switch to an existing library",
      type: "button",
      method: "POST",
      url: "/admin/clear-library",
      icon: "switch_folder"
    },
    {
      text: "Exit PhotoStructure",
      title:
        "Shut down PhotoStructure. Your library will no longer be accessible until you re-launch PhotoStructure.",
      type: "button",
      method: "POST",
      url: "/admin/shutdown",
      icon: "power"
    }
  ])
}
