import { blank } from "../../fe/Blank"
import { later } from "../../fe/Delay"
import { lazy } from "../../fe/Lazy"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { ver } from "../Ver"
import { stdout_ } from "../child/ChildProcess"
import { errorToS } from "../error/Error"
import { ee } from "../event/EventEmitter"
import { PosixFile } from "../fs/PosixFile"
import { isMac } from "../platform/Platform"
import { Settings } from "../settings/Settings"
import { heif2png_, heifConvertPath } from "./HeifConvert"
import { ToolDetails } from "./ToolDetails"
import { sips2jpeg_, sipsPath } from "./sips"

const logger = lazy(() => mkLogger("img.Heif"))

export const getHeifSupportDetails = lazy<Promise<ToolDetails>>(async () => {
  try {
    if (isMac) {
      try {
        const msg = await sipsPath()
        if (!blank(msg)) {
          return { ok: true, msg }
        }
      } catch (err) {
        logger().error(
          "Failed to find the sips tool (either in PATH or /usr/bin).",
          err
        )
      }
    }

    const path = await heifConvertPath()
    if (blank(path)) {
      return {
        ok: false,
        msg: Settings.heifConvertPath.valueOrDefault + ": not installed"
      }
    } else {
      let errs = ""
      const output = await stdout_(path, [], {
        timeoutMs: ShortCommandTimeoutMs,
        quiet: true,
        ignoreExitCode: true, // help message returns 5
        isIgnorableError: err => {
          errs += err.message
          return true
        }
      })
      // Video tools use `${toolName} ${version}` for msg:
      return {
        ok: true,
        version: map(extractHeifConvertVersion(output + errs), ver),
        path,
        msg: "ok"
      }
    }
  } catch (err) {
    return {
      ok: false,
      msg: errorToS(err)
    }
  }
})

export function extractHeifConvertVersion(helptext: string): Maybe<string> {
  // version 1.14 and later will emit version information to stderr:

  // $ heif-convert.exe
  // heif-convert  libheif version: 1.14.0
  // -------------------------------------------
  // Usage: heif-convert [options]  <input-image> <output-image>

  // The program determines the output file format from the output filename suffix.
  // These suffices are recognized: jpg, jpeg, png, y4m.
  // Options:
  //  -h, --help              show help
  //  -q, --quality           quality (for JPEG output)
  //      --with-aux          also write auxiliary images (e.g. depth images)
  //      --with-xmp          write XMP metadata to file (output filename with .xmp suffix)
  //      --with-exif         write EXIF metadata to file (output filename with .exif suffix)
  //      --skip-exif-offset  skip EXIF metadata offset bytes
  //      --no-colons         replace ':' characters in auxiliary image filenames with '_'
  //      --quiet             do not output status messages to console

  // Versions before 1.14:

  // USAGE: heif-convert [-q quality 0..100] <filename> <output>

  return toS(helptext).match(
    // the /s allows the version string to line wrap:
    /libheif version.*?(?<version>[\d.]{5,})\b/is
  )?.groups?.version
}

export async function isHeifSupported() {
  return (await getHeifSupportDetails()).ok
}

/**
 * Converts the given HEIF to either a JPEG or PNG, depending on the platform
 */
export async function heif2img(src: PosixFile) {
  if (isMac) {
    return sips2jpeg_(src)
  } else if (await isHeifSupported()) {
    return heif2png_(src)
  } else {
    logger().throw("HEIF support is missing", {
      src: src.nativePath
    })
    return
  }
}

later(() => ee().on("clearCache", () => getHeifSupportDetails.unset()))
