import { isNotEmpty } from "../../fe/Array"
import { b, li, tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { plur } from "../../fe/Units"
import { errorToS } from "../error/Error"
import { PosixFile } from "../fs/PosixFile"
import { importFileSettings_ } from "../settings/SettingsIO"
import { systemSettingsFile } from "../settings/SystemSettings"
import { HealthCheck, HealthCheckTest } from "./HealthCheck"

export async function validateSettingsToml(
  file: PosixFile,
  flavor: "system" | "library"
): Promise<HealthCheckTest> {
  try {
    const result = await importFileSettings_(PosixFile.for(file))
    if (isNotEmpty(result.warnings)) {
      return {
        level: "warn",
        msg: tt(file) + ": " + result.warnings.join(", ")
      }
    } else {
      return {
        level: "ok",
        msg: [
          plur(result.settings.length, flavor + " setting override"),
          b("Source:"),
          tt(file),
          b("Settings:"),
          li(result.settings.map(s => tt(s.key + "=" + s.toEnvValue(s.value))))
        ]
      }
    }
  } catch (error) {
    return {
      level: "error",
      msg: "Failed to parse " + file + ":\n" + errorToS(error)
    }
  }
}

export const settingsSystemHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Library",
    id: "settings-system",
    pendingMsg: "Checking system settings",
    links: [
      {
        text: "Read about PhotoStructure system settings",
        url: "https://photostructure.com/getting-started/advanced-settings/#system-settings",
        icon: "settings"
      }
    ],

    later: async () => {
      const file = PosixFile.forMaybe(systemSettingsFile())
      if (file == null) {
        return {
          level: "error",
          msg: "No configuration directory was found."
        }
      } else {
        return validateSettingsToml(file, "system")
      }
    }
  })
)
