import { defer } from "../../fe/Defer"
import { librarySettingsFile } from "../settings/SettingsIO"
import { HealthCheck } from "./HealthCheck"
import { validateSettingsToml } from "./SettingsSystemHealthCheck"

export const settingsLibraryHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Library",
    id: "settings-library",
    pendingMsg: "Checking library settings",
    links: [
      {
        text: "Read about PhotoStructure library settings",
        url: "https://photostructure.com/getting-started/advanced-settings/#library-settings",
        icon: "settings"
      }
    ],

    later: async () => {
      const file = librarySettingsFile()
      if (file == null || (await file.notExists())) {
        return {
          level: "disabled",
          msg: "Library settings test disabled: no library is open"
        }
      } else {
        return validateSettingsToml(file, "library")
      }
    }
  })
)
