import p from "process"
import { tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { isSupportedNode, RequiredNodeVersions } from "../NodeVersion"
import { ver } from "../Ver"
import { HealthCheck } from "./HealthCheck"

export const nodejsHealthCheck = defer(() =>
  HealthCheck.for({
    section: "System",
    id: "nodejs-version",
    pendingMsg: "Checking Node.js",
    // ttlMs, // this won't change at runtime
    links: [
      {
        text: "Read about PhotoStructure NodeJS support",
        url: "https://photostructure.com/server/photostructure-for-node/#prerequisites"
      }
    ],
    later: async () =>
      isSupportedNode()
        ? {
            level: "ok",
            msg: [
              "Node.js is a supported version",
              "Node.js " + ver(p.versions.node)
            ]
          }
        : {
            level: "warn",
            msg: [
              "Node.js is not a supported version",
              "PhotoStructure requires " +
                tt(RequiredNodeVersions) +
                ", but you've got " +
                ver(p.versions.node)
            ]
          }
  })
)
