export interface HealthCheckTest
  extends Partial<Pick<HealthCheckResult, "level" | "links">> {
  // no buttons!
  msg: string | Maybe<string>[]
}

export const HealthCheckLevelToLogLevel: Record<HealthCheckLevel, LogLevel> = {
  error: "error",
  "no-library": "info",
  pending: "debug",
  "stop-sync": "info",
  warn: "warn",
  ok: "info",
  disabled: "debug"
}

const HealthLevelToRunState: Pick<
  HealthCheckSummary,
  "level" | "state" | "msg" | "links" | "buttons"
>[] = [
  {
    level: "error",
    state: "failed",
    msg: [
      "Some health checks failed.",
      "Your library won't be synced until these issues are addressed."
    ],
    buttons: restartResetOrShutdownButtons()
  },
  {
    level: "no-library",
    state: "welcome",
    msg: ["No library is currently open."],
    links: [ifNoLibraryLink()],
    buttons: setupLibraryButtons()
  },
  {
    level: "pending",
    state: "loading",
    msg: ["Some health checks are still pending."]
  },
  {
    level: "stop-sync",
    state: "ready",
    msg: [
      "Some health checks are preventing your library from being synced.",
      "You should still be able to view your library, however."
    ]
  },
  {
    level: "warn",
    state: "ready",
    msg: [
      "Some health checks are not OK.",
      "Your library will still be synced, but you may want to address these issues."
    ]
  }
]

const logger = lazy(() => mkLogger("health.HealthCheck"))

export interface RepairTask extends Pick<HealthCheckResult, "id" | "section"> {
  (): Promise<void>
}

export type EnabledHealthCheckLevel = Exclude<HealthCheckLevel, "disabled">

export interface HealthCheckWatcher {
  (result: {
    hcr: HealthCheckResult
    hc: HealthCheck
    priorLevel: HealthCheckLevel
  }): void
}

export class HealthCheck {
  /**
   * Deferred that need to resolve or reject before #isLoading can be set to false
   */
  static #pending: Deferred<any>[] = []

  static #vacuumPending() {
    filterInPlace(this.#pending, ea => !ea.isPending)
    return this.#pending
  }

  static traceLater_<T>(
    desc: string,
    fn: SyncOrAsync<T> | (() => SyncOrAsync<T>)
  ): Deferred<T> | Promise<T> {
    const d = new Deferred<T>(desc)
    void d.catch(error => {
      this.#errors.push(desc + ": failed: " + errorToS(error))
      this.onResultChange()
    })
    this.#vacuumPending().push(d)
    try {
      return d.observe(tot(fn))
    } catch (error) {
      void d.reject(error)
      throw error
    }
  }

  static #errors = new BoundedList<string>(10)
  static #loadingMsgs = new BoundedList<{ at: number; msg: string }>(100)
  static #shownMsgs = new CountingSet<string>()
  static #instances: HealthCheck[] = []
  static readonly all = lazy(() =>
    Object.freeze(
      sortBy(this.#instances, ea => [
        HealthCheckSections.indexOf(ea.section) ?? HealthCheckSections.length,
        ea.id
      ])
    )
  )

  static addError(error: Error | string | any) {
    this.#errors.push(isString(error) ? error : errorToS(error))
    this.onResultChange()
  }

  static addLoadingMsg(msg: string) {
    this.#loadingMsgs.push({ at: Date.now(), msg })
  }

  static normalizeMsg(msg: string | Maybe<string>[]) {
    return compact(toA(msg))
      .map(toS)
      .join("\n")
      .replace(/\n{3,}/g, "\n\n") // no more than 2 blank lines
      .replace(/\n{2,}  - /g, "\n  - ") // no blank lines before or between bullets
      .trim() // no blank lines at start or end
      .split("\n")
  }

  static unseenOrOldestPendingTrace() {
    const pending = this.all()
      .map(ea => ea.result())
      .filter(ea => ea.isPending)
    pending.push(...this.#vacuumPending())
    logger().info("unseenOrOldestPendingTrace", {
      pending: pending.map(ea => ({ name: ea.name, id: ea.id })),
      shownCounts: this.#shownMsgs.entriesByCountDesc()
    })

    // Show oldest unshown message first:
    // If there's a message I haven't shown yet, that's great, show that.
    return map(
      leastBy(pending, ea => [this.#shownMsgs.get(ea.name), ea.startedAt]),
      ea => ({ msg: ea.name, at: ea.startedAt })
    )
  }

  static loadingMsg() {
    const result =
      this.#loadingMsgs.shift() ?? this.unseenOrOldestPendingTrace()
    map(result, ea => this.#shownMsgs.incr(ea.msg))
    return logger().tap({
      msg: "loadingMsg",
      level: "info",
      result: result?.msg,
      meta: {
        runState: this.summary().state
      }
    })
  }

  static findById(id: string) {
    return this.#instances.find(ea => ea.id === id)
  }

  static testResults(): Deferred<HealthCheckResult>[] {
    return this.all().map(ea => ea.result())
  }

  static async errorResults(): Promise<HealthCheckResult[]> {
    return (await Promise.all(this.testResults())).filter(
      ea => ea.level === "error"
    )
  }

  static runState() {
    return this.summary().state
  }

  // We can't use summary.prior() because that gets cleared whenever any
  // healthcheck gets reset.
  static #priorSummary: HealthCheckSummary | undefined

  static readonly summary = lazy(() => this.#summary())

  static summaryFromResults(results: HealthCheckResult[]) {
    this.summary.set(this.#summary(results))
  }

  static #summary(results?: HealthCheckResult[]): HealthCheckSummary {
    return (this.#priorSummary = this.#_summary(results))
  }

  /** DO NOT USE. See #summary() or summary() */
  static #_summary(results?: HealthCheckResult[]): HealthCheckSummary {
    // once we're settled, we need to stay settled:
    const priorSettled = this.#priorSummary?.settled

    if (this.#errors.length > 0) {
      return {
        ...BaseHealthCheckSummary,
        level: HealthCheckLevels.error,
        settled: priorSettled ?? false,
        ts: Date.now(),
        state: RunStates.failed,
        msg: [
          "Errors encountered during setup:",
          uniq(this.#errors.map(ea => errorToS(ea))).join("\n---\n")
        ],
        buttons: restartResetOrShutdownButtons()
      }
    }

    results ??= this.all().map(hc => hc.toResultSync())

    const settled =
      priorSettled === true || results.every(ea => ea.level !== "pending")

    for (const args of HealthLevelToRunState) {
      const arr = results.filter(ea => ea.level === args.level)
      if (isNotEmpty(arr)) {
        if (args.level === "pending" && priorSettled === true) {
          // crap. we're supposed to be settled, but a result is pending.
          logger().info("#summary(): pending result after prior settled", {
            pending: arr,
            priorSettled,
            priorSummary: this.#priorSummary
          })
          // Can we return the last summary?
          if (this.#priorSummary != null) return this.#priorSummary
        }

        // Any other messages we should show?
        const arr2 = results.filter(
          ea =>
            !arr.includes(ea) &&
            ea.level !== "ok" &&
            ea.level !== "disabled" &&
            ea.level !== "pending"
        )

        return logger().tap({
          msg: "summary() found " + arr.length + " " + args.level + " checks",
          level: HealthCheckLevelToLogLevel[args.level],
          result: {
            ...BaseHealthCheckSummary,
            level: args.level,
            state: args.state,
            links: args.links,
            buttons: args.buttons,
            settled,
            linkIds: arr.map(ea => ea.id),
            ts: Date.now(),
            msg: [
              ...args.msg,
              ...(args.level === HealthCheckLevels.pending
                ? []
                : uniq(
                    flatten(
                      [...arr, ...arr2].map(ea =>
                        map(ea.msg[0], str => ea.id + ": " + str)
                      )
                    )
                  ))
            ]
          },
          meta: { checks: arr.map(ea => ea.id) }
        })
      }
    }

    return logger().tap({
      msg: "summary(): all are passing",
      result: {
        ...BaseHealthCheckSummary,
        level: HealthCheckLevels.ok,
        state: RunStates.ready,
        settled,
        ts: Date.now(),
        msg: [
          "All health checks pass.",
          "Last updated: " + fmtDateShort(Date.now())
        ]
      }
    })
  }

  static allResults(): AsyncGenerator<HealthCheckResult> {
    return Deferred.toAsyncIterable(this.all().map(ea => ea.result()))
  }

  static async awaitSettled(): Promise<HealthCheckSummary> {
    if (!HealthCheck.summary().settled) {
      logger().info("awaitSettled(): waiting for all to settle...", {
        initialSummary: HealthCheck.summary()
      })
      const results = []
      for await (const healthCheckResult of this.allResults()) {
        results.push(healthCheckResult)
        if (["error", "no-library"].includes(healthCheckResult.level)) {
          // This result may change the summary:
          HealthCheck.summaryFromResults(results)
        }
      }
      HealthCheck.summaryFromResults(results)
    }
    return logger().tap({
      msg: "awaitSettled(): done",
      level: "info",
      result: HealthCheck.summary()
    })
  }

  static rerunSetup() {
    this.reset()
    return this.awaitSettled()
  }

  static onResultChange() {
    setImmediate(() => HealthCheck.summary.unset())
  }

  static reset() {
    this.onResultChange()
    this.#errors.clear()
    this.#loadingMsgs.clear()
    this.#shownMsgs.clear()
    this.#pending.length = 0
    this.#priorSummary = undefined
    for (const ea of this.all()) {
      ea.reset()
    }
  }

  static for({
    section,
    id,
    pendingMsg,
    later,
    ordinal,
    links,
    okLinks,
    warnLinks,
    noLibraryLinks,
    errorLinks,
    onReset,
    ttlMs,
    timeoutMs,
    rejectIsWarn
  }: {
    section: HealthCheckSection
    id: HealthCheckId
    pendingMsg: string
    later: Later<HealthCheckTest>
    ordinal?: number
    links?: Link[]
    okLinks?: Link[]
    warnLinks?: Link[]
    noLibraryLinks?: Link[]
    errorLinks?: Link[]
    onReset?: () => void
    ttlMs?: number
    timeoutMs?: number
    rejectIsWarn?: boolean
  }) {
    const prior = this.findById(id)
    if (prior != null) {
      throw new Error(
        `Health check with id ${id} already exists in section ${prior.section}`
      )
    }
    const result = new HealthCheck(
      section,
      ordinal == null ? id : ordinal + "-" + id,
      pendingMsg,
      later,
      links,
      okLinks,
      warnLinks,
      noLibraryLinks,
      errorLinks,
      onReset,
      ttlMs,
      timeoutMs,
      rejectIsWarn
    )
    this.#instances.push(result)
    this.all.unset()
    return result
  }

  readonly logger: Logger
  readonly result: MemoizedDeferred<HealthCheckResult>

  private constructor(
    readonly section: HealthCheckSection,
    readonly id: string,
    readonly pendingMsg: string,
    later: Later<HealthCheckTest>,
    private readonly links?: Link[],
    private readonly okLinks?: Link[],
    private readonly warnLinks?: Link[],
    private readonly noLibraryLinks?: Link[],
    private readonly errorLinks?: Link[],
    readonly onClear?: () => void,
    readonly ttlMs?: number,
    readonly timeoutMs?: number,
    readonly rejectIsWarn = false
  ) {
    this.logger = mkLogger("HealthCheck(" + this.id + ")")
    this.result = lazyAsync({
      desc: this.pendingMsg,
      // ensure this.#lazy never throws:
      later: () => this.#wrapLater(later),
      ttlMs,
      timeoutMs
    })
  }

  isPending() {
    return this.result.isStale()
  }

  settledAtLeastOnce() {
    return this.result.settledCount()
  }

  /**
   * Clear the prior test result so that it will be re-evaluated on next
   * access.
   */
  reset() {
    this.result.unset()
    this.onClear?.()
    HealthCheck.summary.unset()
  }

  /**
   * Rerun the test now
   */
  refresh() {
    return this.result.refresh()
  }

  isSkipped() {
    return Settings.skipHealthCheckIds.values.includes(this.id)
  }

  async #wrapLater(later: Later<HealthCheckTest>): Promise<HealthCheckResult> {
    logger().debug("#wrapLater() starting", { id: this.id })
    if (this.isSkipped()) {
      return this.#toResult({
        level: "disabled",
        msg: ["Disabled by " + Settings.skipHealthCheckIds.key]
      })
    }
    try {
      const timeoutMs = this.timeoutMs ?? commandTimeoutMs()
      const result = await thenOrTimeoutError(
        getDevEnvFlag("PS_SLOMO")
          ? // make .8 to avoid timeout, or 1.5 to exercise timeout error handling:
            delay(randomInt(secondMs, timeoutMs * 0.3)).then(later)
          : later(),
        timeoutMs / 2
      )
      return this.#toResult(result)
    } catch (err) {
      return this.#toErrorResult(err)
    } finally {
      HealthCheck.onResultChange()
    }
  }

  // All results go through this method
  #toResult(test: HealthCheckTest, src = "#toResult"): HealthCheckResult {
    // normalize lines:
    const msg = HealthCheck.normalizeMsg(test.msg)
    const level =
      test.level ??
      (toS(msg).toLowerCase().includes("error")
        ? "error"
        : toS(msg).toLowerCase().includes("warn")
        ? "warn"
        : "ok")
    const links = compact([
      ...(test?.links ??
        (level === "ok" || level === "pending"
          ? this.okLinks
          : level === "warn"
          ? this.warnLinks
          : level === "no-library"
          ? this.noLibraryLinks
          : level === "error"
          ? this.errorLinks
          : []) ??
        []),
      ...(this.links ?? [])
    ])
    const result: HealthCheckResult = {
      section: this.section,
      id: this.id,
      ts: Date.now(),
      level,
      elapsedMs: this.result.elapsedMs(),
      msg,
      ...partitionLinks(links)
    }
    // don't bother to log if we're ending
    return ending()
      ? result
      : this.logger.tap({
          level: HealthCheckLevelToLogLevel[level],
          msg: src,
          result
        })
  }

  #toErrorResult(error: any, level?: HealthCheckLevel): HealthCheckResult {
    this.logger.warn("#toErrorResult", { error, level })
    const msg =
      stripSuffix(this.pendingMsg, "") +
      " failed:\n" +
      (map(error, errorToS) ?? "Unknown error")
    level ??=
      this.rejectIsWarn ||
      (!isFatalError(error) && msg.toLowerCase().includes("warn"))
        ? "warn"
        : "error"
    return this.#toResult({ msg, level }, "#toErrorResult")
  }

  toLevelSync() {
    return this.result.prior()?.value?.level ?? "pending"
  }

  toResultSyncMaybe(): HealthCheckResult | undefined {
    if (this.result.isStale()) {
      setImmediate(
        () => this.result() // < ensure it's fresh
      )
    }
    return this.result.lastValue()
  }

  toResultSync(): HealthCheckResult {
    return (
      this.toResultSyncMaybe() ?? {
        section: this.section,
        id: this.id,
        ts: Date.now(),
        level: "pending",
        msg: [this.pendingMsg]
      }
    )
  }

  async resultOrTimeout(timeoutMs: number): Promise<HealthCheckResult> {
    return (
      (await thenOrTimeoutAs({ f: this.result(), timeoutMs, as: undefined })) ??
      this.#toErrorResult("Timeout", "warn")
    )
  }
}

function partitionLinks(
  arr: Maybe<Link[]>
): Pick<HealthCheckResult, "links" | "buttons"> {
  if (isEmpty(arr)) return {}
  const [buttons, links] = partition(
    arr,
    ea => ea.type === "button" || ea.method === "POST"
  )
  const result = {} as Pick<HealthCheckResult, "links" | "buttons">
  if (isNotEmpty(links)) result.links = uniqBy(links)
  if (isNotEmpty(buttons)) result.buttons = uniqBy(buttons)
  return result
}
